import { Api } from './base/api';
export class ShopApi extends Api {
    constructor(cdn, baseUrl, options) {
        super(baseUrl, options);
        this.cdn = cdn;
    }
    getProductList() {
        return this.get('/product').then((data) => data.items.map((item) => (Object.assign({}, item))));
    }
    orderProducts(order) {
        return this.post('/order', order).then((data) => data);
    }
}
